// 
@mixin content-ellipsis($line-num) {
    // 
    overflow: hidden;
    text-overflow: ellipsis;
    // 
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line-num;
}

// 
@mixin img-radius-top {
    border-radius: 526px 526px 0px 0px;
}

// 
@mixin img-radius-bottom {
    border-radius: 0px 0px 526px 526px;
}